<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="caigoudanhao">
				<el-input v-model="ruleForm.caigoudanhao" placeholder="" disabled></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="wupinmingcheng">
				<el-input v-model="ruleForm.wupinmingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.wupinmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="wupinleixing">
				<el-input v-model="ruleForm.wupinleixing" 
					placeholder="" clearable :disabled=" false  ||ro.wupinleixing"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="guige">
				<el-input v-model="ruleForm.guige" 
					placeholder="" clearable :disabled=" false  ||ro.guige"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shuliang">
				<el-input v-model.number="ruleForm.shuliang" 
					placeholder="" clearable :disabled=" false  ||ro.shuliang"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.wupintupian)" prop="wupintupian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.wupintupian?ruleForm.wupintupian:''"
					@change="wupintupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="wupintupian">
				<img v-if="ruleForm.wupintupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.wupintupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.wupintupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="rukushijian">
				<el-date-picker
					:disabled="true  ||ro.rukushijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.rukushijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="rukubeizhu">
				<el-input v-model="ruleForm.rukubeizhu" 
					placeholder="" clearable :disabled=" false  ||ro.rukubeizhu"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					caigoudanhao : false,
					wupinmingcheng : false,
					wupinleixing : false,
					guige : false,
					shuliang : false,
					wupintupian : false,
					rukushijian : false,
					rukubeizhu : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					caigoudanhao: this.getUUID(),
					wupinmingcheng: '',
					wupinleixing: '',
					guige: '',
					shuliang: '',
					wupintupian: '',
					rukushijian: '',
					rukubeizhu: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
				},


				rules: {
					caigoudanhao: [
					],
					wupinmingcheng: [
					],
					wupinleixing: [
					],
					guige: [
					],
					shuliang: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					wupintupian: [
					],
					rukushijian: [
					],
					rukubeizhu: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.rukushijian = this.getCurDateTime()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='caigoudanhao'){
							this.ruleForm.caigoudanhao = obj[o];
							this.ro.caigoudanhao = true;
							continue;
						}
						if(o=='wupinmingcheng'){
							this.ruleForm.wupinmingcheng = obj[o];
							this.ro.wupinmingcheng = true;
							continue;
						}
						if(o=='wupinleixing'){
							this.ruleForm.wupinleixing = obj[o];
							this.ro.wupinleixing = true;
							continue;
						}
						if(o=='guige'){
							this.ruleForm.guige = obj[o];
							this.ro.guige = true;
							continue;
						}
						if(o=='shuliang'){
							this.ruleForm.shuliang = obj[o];
							this.ro.shuliang = true;
							continue;
						}
						if(o=='wupintupian'){
							this.ruleForm.wupintupian = obj[o]?obj[o].split(",")[0]:'';
							this.ro.wupintupian = true;
							continue;
						}
						if(o=='rukushijian'){
							this.ruleForm.rukushijian = obj[o];
							this.ro.rukushijian = true;
							continue;
						}
						if(o=='rukubeizhu'){
							this.ruleForm.rukubeizhu = obj[o];
							this.ro.rukubeizhu = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
					}
					this.ruleForm.shuliang = 0;
					this.ro.shuliang = false;
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`caigoujilu/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(this.ruleForm.caigoudanhao){
					this.ruleForm.caigoudanhao = String(this.ruleForm.caigoudanhao)
				}
				if(!this.ruleForm.shuliang&&this.ruleForm.shuliang!=0){
					this.$message.error("");
					return
				}
				var obj = JSON.parse(localStorage.getItem('crossObj'));
				var table = localStorage.getItem('crossTable');
				obj.shuliang = Number((parseFloat(obj.shuliang) + parseFloat(this.ruleForm.shuliang)).toFixed(2))
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}
						// 
						var obj = JSON.parse(localStorage.getItem('crossObj'));
						var table = localStorage.getItem('crossTable');

						obj.shuliang = Number((parseFloat(obj.shuliang) + parseFloat(this.ruleForm.shuliang)).toFixed(2))

						await this.$http.post(table+`/update`,obj).then(res => {});
						await this.$http.post(`caigoujilu/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			wupintupianUploadChange(fileUrls) {
				this.ruleForm.wupintupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

