<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="shebeimingcheng">
				<el-input v-model="ruleForm.shebeimingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.shebeimingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="shebeileixing">
				<el-select v-model="ruleForm.shebeileixing" placeholder="" :disabled=" false  ||ro.shebeileixing" >
					<el-option
						v-for="(item,index) in shebeileixingOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="guige">
				<el-input v-model="ruleForm.guige" 
					placeholder="" clearable :disabled=" false  ||ro.guige"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xinghao">
				<el-input v-model="ruleForm.xinghao" 
					placeholder="" clearable :disabled=" false  ||ro.xinghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shuliang">
				<el-input v-model.number="ruleForm.shuliang" 
					placeholder="" clearable :disabled=" false  ||ro.shuliang"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.shebeitupian)" prop="shebeitupian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.shebeitupian?ruleForm.shebeitupian:''"
					@change="shebeitupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="shebeitupian">
				<img v-if="ruleForm.shebeitupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.shebeitupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.shebeitupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xunjianzhouqi">
				<el-input v-model="ruleForm.xunjianzhouqi" 
					placeholder="" clearable :disabled=" false  ||ro.xunjianzhouqi"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xunjianneirong">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.xunjianneirong">
					</el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					shebeimingcheng : false,
					shebeileixing : false,
					guige : false,
					xinghao : false,
					shuliang : false,
					shebeitupian : false,
					xunjianzhouqi : false,
					xunjianneirong : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					shebeimingcheng: '',
					shebeileixing: '',
					guige: '',
					xinghao: '',
					shuliang: '',
					shebeitupian: '',
					xunjianzhouqi: '',
					xunjianneirong: '',
				},
				shebeileixingOptions: [],


				rules: {
					shebeimingcheng: [
					],
					shebeileixing: [
					],
					guige: [
					],
					xinghao: [
					],
					shuliang: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					shebeitupian: [
					],
					xunjianzhouqi: [
					],
					xunjianneirong: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='shebeimingcheng'){
							this.ruleForm.shebeimingcheng = obj[o];
							this.ro.shebeimingcheng = true;
							continue;
						}
						if(o=='shebeileixing'){
							this.ruleForm.shebeileixing = obj[o];
							this.ro.shebeileixing = true;
							continue;
						}
						if(o=='guige'){
							this.ruleForm.guige = obj[o];
							this.ro.guige = true;
							continue;
						}
						if(o=='xinghao'){
							this.ruleForm.xinghao = obj[o];
							this.ro.xinghao = true;
							continue;
						}
						if(o=='shuliang'){
							this.ruleForm.shuliang = obj[o];
							this.ro.shuliang = true;
							continue;
						}
						if(o=='shebeitupian'){
							this.ruleForm.shebeitupian = obj[o]?obj[o].split(",")[0]:'';
							this.ro.shebeitupian = true;
							continue;
						}
						if(o=='xunjianzhouqi'){
							this.ruleForm.xunjianzhouqi = obj[o];
							this.ro.xunjianzhouqi = true;
							continue;
						}
						if(o=='xunjianneirong'){
							this.ruleForm.xunjianneirong = obj[o];
							this.ro.xunjianneirong = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.$http.get('option/shebeileixing/shebeileixing', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.shebeileixingOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`xunjianjihua/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`xunjianjihua/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			shebeitupianUploadChange(fileUrls) {
				this.ruleForm.shebeitupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

